export default{
  data() {
    return {
      info: {
        name: '',
        tel: '',
        location: '',
        address: '',
        tags: ['家', '公司', '学校', '其他'],
        def: false
      }
    }
  },
  onLoad(option) {
    if(option.info) {
      this.info = JSON.parse(option.info)
    }
  },
  computed: {
    statusBarHeight() {
      return this.$store.state.statusBarHeight
    }
  },
  methods: {
    goBack() {
      uni.navigateBack({
        delta: 1
      })
    },
    // 打开地图选择地址并获取位置
    getLocation() {
      let that = this
      uni.chooseLocation({
        success: function (res) {
          that.info.location = res.address
          console.log('位置名称：' + res.name);
          console.log('详细地址：' + res.address);
          console.log('纬度：' + res.latitude);
          console.log('经度：' + res.longitude);
        }
      })
    },
    delAction() {
      uni.showModal({
        // title: '温馨提示',
        content: '确认删除？',
        success(res) {
          if (res.confirm) {
            console.log(res.confirm)
          } else if (res.cancel) {
            console.log('用户点击取消')
          }
        }
      })
    },
    switchChange(e) {
      console.log('switch1 发生 change 事件，携带值为', e.target.value)
    }
  }
}
